import { Box, VStack, Text, HStack, Image, Fade } from "@chakra-ui/react";
import {
    AnimatePresence,
    motion,
    useMotionValueEvent,
    useScroll,
} from "framer-motion";
import { useState } from "react";
import sample_1 from "../../images/sample_1.PNG";
import sample_2 from "../../images/sample_2.PNG";
const textVariants = {
    initial: { opacity: 0, scale: 0 },
    visible: {
        opacity: 1,
        scale: 1.0,
        transition: {
            duration: 1.1,
        },
    },
    exit: {
        opacity: 1,
        scale: 1,
    },
};

export default function Section2() {
    const { scrollY } = useScroll();
    const [showText, setShowText] = useState(false);
    const [showPic, setShowPic] = useState(false);
    useMotionValueEvent(scrollY, "change", (y) => {
        if (y > 40) {
            setShowText(true);
        } else if (y < 30) {
            setShowText(false);
        }
    });
    useMotionValueEvent(scrollY, "change", (y) => {
        if (y > 180) {
            setShowPic(true);
        } else if (y < 170) {
            setShowPic(false);
        }
    });
    return (
        <>
            <section
                style={{ padding: "3rem 0 3rem 0", backgroundColor: "#EAE9D3" }}
            >
                {/* #F8F8F8 */}
                <VStack>
                    <Box
                        fontWeight={"400"}
                        textAlign={"center"}
                        color={"black"}
                    >
                        <h1 style={{ fontSize: "22px", fontWeight: "700" }}>
                            <strong>  ?</strong>
                        </h1>
                        <Text>
                               <br></br>  
                              !
                        </Text>
                    </Box>
                    <AnimatePresence initial={false}>
                        {showText ? (
                            <Box
                                as={motion.div}
                                key="text1"
                                variants={textVariants}
                                initial="initial"
                                animate="visible"
                                exit="exit"
                                my={4}
                                color={"black"}
                                fontWeight={"600"}
                                fontSize={"24px"}
                            >
                                <Text>   ?</Text>
                            </Box>
                        ) : null}
                    </AnimatePresence>
                    <VStack mt={8}>
                        <Fade
                            in={showPic}
                            transition={{ enter: { duration: 2.2 } }}
                            style={{
                                display: "flex",
                                justifyContent: "center",
                                alignItems: "center",
                                flexDirection: "column",
                                gap: "15px",
                            }}
                        >
                            <Image
                                w={"80%"}
                                boxShadow={"0 6px 15px 0px rgba(0,0,0,0.5)"}
                                src={sample_1}
                            />
                            <Image
                                boxShadow={"0 6px 15px 0px rgba(0,0,0,0.5)"}
                                w={"80%"}
                                src={sample_2}
                            />
                        </Fade>

                        <Text color={"gray.500"} fontSize={"14px"}>
                            Instead of me .
                        </Text>
                        <Box
                            mt={6}
                            color={"black"}
                            fontWeight={"500"}
                            textAlign={"center"}
                            fontSize={"17px"}
                        >
                            <Text>     </Text>
                            <Text>
                                 {" "}
                                <strong style={{ fontWeight: "700" }}>
                                    
                                </strong>{" "}
                                 .
                            </Text>
                            <Text>   ,</Text>
                            <Text>
                                 10  .
                            </Text>
                        </Box>
                    </VStack>
                </VStack>
            </section>
        </>
    );
}
